@import 'commons';

.app {
    flex-direction: column;
    min-height: 100vh;
    background-color: hsl(var(--background));
    color: hsl(var(--primary));

    .main-header {
        padding: 1rem;
        text-align: center;
    }

    main {
        flex-grow: 1;
    }

    .main-footer {
        .container {
            justify-content: flex-end;
            align-items: center;

            * {
                margin: 10px;
            }

            p {
                padding: 18px 0;
                text-align: right;
                font-style: italic;
            }

            .icon {
                height: 24px;
            }
        }
    }

}
